import Navbar from "../components/Navbar";


const About = () => (
    <div>
        <Navbar />
        <h1 style={linkStyle}>Gerilimsiz</h1>
        <p>Gerilimsiz is the stress reduction App that would help you schedule and manage your workflow by adding and deleting tasks todo, thereby reducing workload stress.</p>
      <h3>About App</h3>
    </div>
    
);

const linkStyle = {
    style: 'italics',
    weight: 'bold',
    color: '#000',
    marginRight: '15px',
}

const navStyle = {
    background: '#3367d6',
    textAlign: 'right',
    padding: '10px',
    color: '#fff'
}

export default About;
